import { CSSTransition } from 'react-transition-group';
import './HelpStyle.css';

function HelpScreen(props) {
  return (
    <CSSTransition in={props.active} classNames="glide-in" timeout={500} unmountOnExit>
      <section className="helpOverlay">
        <button className="closeButton" onClick={props.close}>
          X
        </button>
        <div className="textContainer">{props.children}</div>
      </section>
    </CSSTransition>
  );
}

export default HelpScreen;
