import { Range, DropDownButton, CheckBox, FooterTab, Hint, Button, FirstClicker, LastClicker, MiddleClicker, DropDownItem } from '../../Controls/Controls';

function Footer(props) {
  const data = props.data;

  return (
    <FooterTab>
      <Button>
        <FirstClicker text="Shuffle" onClick={props.startShuffling} />
        <MiddleClicker text="Help" onClick={props.activateHelpScreen} />
        <LastClicker text="Sort" onClick={props.startSorting} />
      </Button>

      <Hint text="Try hovering over these ->" />

      <Range min="10" max="500" step="10" data={data} changeEvent={props.changeEvent} targetVal="count" text={`Count: ${data.count} points`} />
      <Range min="10" max="1000" step="10" data={data} changeEvent={props.speedChange} targetVal="speed" text={`Speed: ${data.speed} ms`} />

      <DropDownButton prefHeight="20vh" targetval="sortType" text="Sorting: " data={data} changeEvent={props.changeEvent}>
        <DropDownItem text="Selection" />
        <DropDownItem text="Insertion" />
        <DropDownItem text="Bubble" />
        <DropDownItem text="Merge" />
        <DropDownItem text="Quick" />
        <DropDownItem text="Bucket" />
      </DropDownButton>

      <DropDownButton prefHeight="20vh" targetval="drawType" text="Draw Mode: " data={data} changeEvent={props.changeEvent}>
        <DropDownItem text="Normal" />
        <DropDownItem text="Center" />
        <DropDownItem text="Angle" />
        <DropDownItem text="Line" />
      </DropDownButton>

      <CheckBox changeEvent={props.speedChange} data={data} targetVal="reverseSort" text="Reverse Sort" />
      <CheckBox changeEvent={props.changeEvent} data={data} targetVal="fastSort" text="Fast sort" />
    </FooterTab>
  );
}

export default Footer;
