import React from 'react';
import p5 from 'p5';
import './sortingStyle.css';
import Footer from './footerControls';
import createSketch from './sketch.js';

import HelpScreen from '../../Controls/HelpScreen';

class Sorting extends React.Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();

    this.handleChange = this.handleChange.bind(this);
    this.handleScreenChange = this.handleScreenChange.bind(this);
    this.handleSpeedChange = this.handleSpeedChange.bind(this);
    this.activateHelp = this.activateHelp.bind(this);

    this.state = {
      height: 0,
      width: 0,
      screenChange: this.handleScreenChange,
      resized: false,
      data: { count: 100, speed: 10, sortType: ['Selection', 0], drawType: ['Normal', 0], reverseSort: false, fastSort: false },
      helpActive: false,
    };
  }

  componentDidMount() {
    const height = document.getElementById('p5js-container').clientHeight;
    const width = document.getElementById('p5js-container').clientWidth;

    this.setState({ height, width });

    this.myP5 = new p5(createSketch(this.state), this.myRef.current);
  }

  componentWillUnmount() {
    this.myP5.remove();
    this.myP5 = undefined;
  }

  handleChange(data) {
    this.setState({ data });
    this.myP5.resetSketch();
  }

  handleSpeedChange(data) {
    this.setState({ data });
    this.myP5.setSpeed(this.state.data.speed);
  }

  handleScreenChange(resized) {
    const height = document.getElementById('p5js-container').clientHeight - 5;
    const width = document.getElementById('p5js-container').clientWidth;

    this.setState({ height, width, resized });

    return this.state;
  }

  activateHelp(helpActive) {
    const oldState = this.state;
    oldState.helpActive = helpActive;
    this.setState(oldState);
  }

  render() {
    return (
      <section key={1} className="sectionClass">
        <div ref={this.myRef} className="p5js" id="p5js-container"></div>
        <Footer
          data={this.state.data}
          changeEvent={this.handleChange}
          speedChange={this.handleSpeedChange}
          startShuffling={() => {
            this.myP5.startShuffling();
          }}
          startSorting={() => {
            this.myP5.startSorting();
          }}
          activateHelpScreen={() => {
            this.activateHelp(true);
          }}
        />

        {createHelpScreen(this.state.helpActive, this.activateHelp)}
      </section>
    );
  }
}

export default Sorting;

// #region Help Screen
function createHelpScreen(active, setActive) {
  return (
    <HelpScreen
      active={active}
      close={() => {
        setActive(false);
      }}
    >
      <div>
        <h3>Background</h3>
        <p>
          A sorting algorithm is an algorithm which puts the elements from a list in an order determined by the user. Although the most common use of sorting
          techniques is numerical ordering, sorting algorithms can be used on any list. The only requirement is that the user specifies a comparison if there
          are none.
        </p>
        <p>
          For more information visit the{' '}
          <a href="https://en.wikipedia.org/wiki/Sorting_algorithm" rel="noreferrer" target="_blank">
            Wikipedia page
          </a>
          .
        </p>
      </div>
      <div>
        <h3>Important Information</h3>
        <p>
          This project is only an visual comparison between a few sorting algorithms.{' '}
          <span className="highlight">This is not a speed comparison between the sorting algorithms.</span> Just because the visualization shows the bubble
          sorting algorithm running faster than the selection, does not mean that bubble sorting is faster, or visa versa. For actual speed comparisons refer to
          the big O notation of each algorithm.
        </p>
        <h4>For mobile users</h4>
        <p>
          Setting the <span>count</span> too <span>high</span> can cause your browser to slow down, and/or crash. I've determined on my cell that my browser
          starts slowing at around 250 data points
        </p>
      </div>
      <div>
        <h3>Project Settings</h3>
        <p>
          - <span>Count</span>: Determines the total amount of data points which will be sorted. Higher number = longer sorting time, Lower number = faster
          sorting time
        </p>
        <p>
          - <span>Speed</span>: Determines the speed at which a switch between data points occur. The fast value is a switch every 10 milliseconds. The slowest,
          a switch every second. Adjust the speed parameter to slow down the sorting algorithm to have a better idea of how the algorithm functions.
        </p>
        <p>
          - <span>Sorting</span>: Hover over this label to switch between sorting methods. This will reset the current data points.
        </p>
        <p>
          - <span>Draw Mode</span>: Hover over this label to switch between the visualization function. Each function visualizes the data in a different way.
          This will reset the current data points.
        </p>
        <p>
          - <span>Reverse Sort</span>: Determines the sorting order. By default it sorts ascending. Switching on the button switches to sorting in an descending
          order. <span className="highlight">This setting does not effect the line visualization.</span>
        </p>
        <p>
          - <span>Fast Sort</span>: For non-recursive functions, switching this mode on causes the sorting algorithm to run once through the entire array before
          waiting the time determined by the speed parameter. For recursive function this causes the function to run fully asynchronously, which means{' '}
          <span>multiple occurrences</span> of the sorting algorithm can happen on subsets of the data.
        </p>
      </div>
      <div>
        <h3>Missing Information</h3>
        <p>
          For any information which isn't clear, or is misleading please contact me at:{' '}
          <a href="mailto:rpnoeth@gmail.com" rel="noreferrer" target="_blank">
            rpnoeth@gmail.com
          </a>
          . With the <span>subject heading </span>of <span>"Projects Site Sorting"</span>
        </p>
      </div>
    </HelpScreen>
  );
}
// #endregion
