import { FooterTab, Button, FirstClicker, LastClicker, Range, DropDownButton, DropDownItem, Hint } from '../../Controls/Controls';

function Footer(props) {
  const data = props.data;

  const activeType = data.activeType;

  const dropdownClicked = (targetVal, newVal) => {
    const newData = props.data;
    newData[targetVal] = newVal;

    props.changeEvent(newData);
  };

  return (
    <FooterTab>
      <Button>
        <FirstClicker text="Start" onClick={props.start} />
        <LastClicker text="Stop" onClick={props.stop} />
      </Button>

      <Hint text="Hover over these ->" />

      <DropDownButton prefHeight="30vh" text="Rule Set: " targetval="activeType" changeEvent={props.changeEvent} data={data}>
        <DropDownItem text="Normal" />
        <DropDownItem text="No Repeat" />
        <DropDownItem text="Neighbours only" />
        <DropDownItem text="Opposite Neighbours Only" />
        <DropDownItem text="Not Anti-Dlockwise" />
        <DropDownItem text="No Double Repeat" />
      </DropDownButton>

      <Range min="3" max="7" step="1" changeEvent={props.changeEvent} data={data} targetVal="vertexCount" text={`Vertex Count: ${data.vertexCount} points`} />

      <Range min="2" max="4" step="1" changeEvent={props.changeEvent} data={data} targetVal="stopLength" text={`Step length: 1/${data.stopLength} step`} />

      <Range min="100" max="2000" step="100" changeEvent={props.changeEvent} data={data} targetVal="speed" text={`Speed: ${data.speed} dots per cycle`} />

      <Range min="20" max="255" step="1" changeEvent={props.changeEvent} data={data} targetVal="opacity" text={`Opacity: ${data.opacity} / 255`} />

      <Hint text="Some mode's wont generate on lower vertex counts" />
    </FooterTab>
  );
}

export default Footer;
