import './ControlStyles.css';
import React from 'react';

// Creates the different styles for buttons
export function FirstClicker(props) {
  return (
    <span onClick={props.onClick} className="button-start">
      {props.text}
    </span>
  );
}

export function MiddleClicker(props) {
  return (
    <span onClick={props.onClick} className="button-middle">
      {props.text}
    </span>
  );
}

export function LastClicker(props) {
  return (
    <span onClick={props.onClick} className="button-end">
      {props.text}
    </span>
  );
}

// Creates a button container
export function Button(props) {
  return <button className="clickable-button">{props.children}</button>;
}

// Creates hint text
export function Hint(props) {
  return (
    <span className="hintspan">
      <strong>{props.text}</strong>
    </span>
  );
}

// Creates a custom footer bar
export function FooterTab(props) {
  return (
    <footer className="controls">
      <div className="footer-container">{props.children}</div>
    </footer>
  );
}

// Creates a drop down
export function DropDownButton(props) {
  const text = props.text ? props.text : '';
  const afterText = props.afterText ? props.afterText : '';
  const active = props.data[props.targetval][0];

  const newHeight = props.prefHeight ? props.prefHeight : 'auto';

  const clicked = (newVal) => {
    const newData = props.data;
    newData[props.targetval] = newVal;

    props.changeEvent(newData);
  };

  return (
    <div className="dropdown" style={{ '--prefHeight': newHeight }}>
      <span>{`${text}${active}${afterText}`}</span>
      <div className="dropdown-content">
        <ul>
          {React.Children.map(props.children, (child, index) => {
            return React.cloneElement(child, { clicked: clicked, index, active }, null);
          })}
        </ul>
      </div>
    </div>
  );
}

// Items specifically for drop downs
export function DropDownItem(props) {
  return (
    <li
      onClick={() => {
        props.clicked([props.text, props.index]);
      }}
      className={props.active === props.text ? 'active' : ''}
    >
      {props.text}
    </li>
  );
}

// Creates a checkbox
export function CheckBox(props) {
  return (
    <div className="chk-container">
      <label>
        <span>{props.text}</span>
        <input
          type="checkbox"
          checked={props.data[props.targetVal]}
          onChange={(event) => {
            const newData = props.data;
            newData[props.targetVal] = event.target.checked;

            props.changeEvent(newData);
          }}
        />
      </label>
    </div>
  );
}

// Creates a popup range
export function Range(props) {
  const newHeight = props.prefHeight ? props.prefHeight : '40px';

  return (
    <div className="dropdown" style={{ '--prefHeight': newHeight }}>
      <span>{props.text}</span>
      <div className="dropdown-content">
        <input
          type="range"
          value={props.data[props.targetVal]}
          onChange={(event) => {
            const newData = props.data;
            newData[props.targetVal] = event.target.value;

            props.changeEvent(newData);
          }}
          min={props.min}
          max={props.max}
          step={props.step}
        />
      </div>
    </div>
  );
}
